@use "../../base/variables" as v;
@use "../../base/mixins" as m;

.formulario {
    margin: 5rem auto 2rem;
    background-color: v.$blanco;
    border-radius: 5rem;
    box-shadow: 0 3px 7px 0px v.$gris;
    padding: 3rem 0;

    @include m.desktop { 
          display: flex;
          justify-content: space-between;
          max-width: 90rem;
          padding: 0;
    }

    &__campo {
        padding: 1rem 2rem;
        display: flex;
        flex-direction: column;
        @include m.desktop { 
            &:nth-child(1){
                padding-left: 4rem;
            } 
        }
        &:hover {
            background-color: #f0f0f0;
            border-radius: 5rem;
            box-shadow: 0px 6px 20px rgb(0 0 0 / 40%);
        }
        &:last-of-type:hover {
            background-color: v.$blanco;
            border-radius: 3rem;
            box-shadow: unset;
            border-top-right-radius: 5rem;
            border-bottom-right-radius: 5rem;
        }
    }

    &__label {
        font-size: 1.2rem;
        font-weight: 900;

    }
    &__input {
        border: none;
        width: 100%;
        color: v.$gris;
    }
    &__submit {
        width: 100%;
        height: 4rem;
        border: none;
        border-radius: 2rem;
        background-image: radial-gradient(circle, rgb(255, 56, 92) 0%, v.$secundario 100% );
        padding: 0 3rem;
        color: v.$blanco;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 1.2rem;

        @include m.telefono { 
              width: 60%;
              margin: 0 auto;
        }
        @include m.desktop { 
            width: 100%;
        }
        
        &:hover {
            cursor: pointer;
            background-image: radial-gradient(circle, #f0f0f0 0%, rgb(0 0 0 / 0.1) 100% );
            color: v.$primario;
            border: 1px solid v.$primario;
        }
    }
}